import React, { useState, useEffect } from 'react'
import Person from './components/Person'
import Notification from './components/Notification'
import personService from './services/persons'

const Persons = ({persons, setPersons, newFilter}) => {

  const deleteFunction = name => {
    const person = persons.find(n => n.name === name)
    if (window.confirm(`Delete ${name}?`)){
      const personid = person.id
      personService
        .del(personid)
        .then(setPersons(persons.filter(p => p.name !== name)))
    }
  }

  const personsToShow = newFilter === ''
  ? persons
  : persons.filter(person => person.name.includes(newFilter))

  const phonebook = () => personsToShow.map(person =>
    <Person
      key={person.name}
      person={person}
      deleteFunction={() => deleteFunction(person.name)}
    />
  )

  return(
    <div>
      {phonebook()}
    </div>
  )
}

const PersonForm = ({persons, setPersons, newName, setNewName, newNumber, setNewNumber, setMessage, setAdded}) => {
  const addPerson = (event) => {
    event.preventDefault()
    let names = persons.map(person => person.name)
    if (names.indexOf(newName) === -1) {
      const personObject = {
        name : newName,
        number : newNumber
      }
      setMessage(`Added ${newName}`)
      setAdded('added')
      setTimeout(() => {
        setMessage(null)
      }, 5000)
      personService
      .create(personObject)
      .then(returnedPerson => {
        setPersons(persons.concat(returnedPerson))
        setNewName('')
        setNewNumber('')
      })
      
    } else {
      if (window.confirm(`${newName} is already added to phonebook, replace the old number with a new one?`)){
        const person = persons.find(n => n.name === newName)
        const personid = person.id
        const personObject = {...person, number: newNumber}
        setMessage(`Updated ${newName}`)
        setAdded('added')
        setTimeout(() => {
          setMessage(null)
        }, 5000)
        personService
          .update(personid, personObject)
          .then(returnedPerson => {
            setPersons(persons.map(p => p.name !== newName ? p : returnedPerson))
            setNewName('')
            setNewNumber('')
          })
          .catch(error =>{
            setMessage(`Information of ${newName} has already been removed from server`)
            setAdded('del')
          })
      }
    }
  }

  const handleNameChange = (event) => {
    setNewName(event.target.value)
  }

  const handleNumberChange = (event) => {
    setNewNumber(event.target.value)
  }

  return(
    <form onSubmit={addPerson}>
      <div>
        name: <input value={newName} onChange={handleNameChange}/>
      </div>
      <div>
        number: <input value={newNumber} onChange={handleNumberChange}/>
      </div>
      <div>
        <button type="submit">add</button>
      </div>
    </form>
  )
}

const Filter = ({newFilter, setNewFilter}) => {
  const handleFilterChange = (event) => {
    setNewFilter(event.target.value)
  }
  return (
    <form>
      <div>
        filter shown with <input value={newFilter} onChange={handleFilterChange}/>
      </div>
    </form>
  )
}

const App = () => {
  const [ persons, setPersons] = useState([]) 
  const [ newName, setNewName ] = useState('')
  const [ newNumber, setNewNumber ] = useState('')
  const [ newFilter, setNewFilter ] = useState('')
  const [ verifiyMessage, setMessage] = useState('')
  const [ added, setAdded] = useState('')

  useEffect(() => {
    personService
      .getAll()
      .then(initialPersons => {
          setPersons(initialPersons)
      })
  }, [])

  return (
    <div>
      <h2>Phonebook</h2>
      <Notification message={verifiyMessage} type={added} />
      <Filter newFilter={newFilter} setNewFilter={setNewFilter}/>
      <h2>add a new</h2>
      <PersonForm
        persons={persons} setPersons={setPersons}
        newName={newName} setNewName={setNewName}
        newNumber={newNumber} setNewNumber={setNewNumber}
        setMessage={setMessage} setAdded={setAdded} />
      <h2>Numbers</h2>
      <Persons persons={persons} setPersons={setPersons} newFilter={newFilter} />
    </div>
  )
}

export default App


